@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  /* background-color: #000; */
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: rgb(241, 241, 241);
}

.app-main-container {
display: grid;
grid-template-columns: repeat(3, 4fr);
grid-template-rows: 350px 100px 350px; /* Adjust height of widgets*/
grid-template-areas: 
"news weather messages"
"news weather messages"
"calendar calendar calendar";

grid-gap: 25px;
margin: 25px 120px;
/* height: 400px; */

}

.news-component, 
.calendar-component, 
.weather-component, 
.messages-component,
.stocks-component {
  background-color: #fff;
  border-radius: 6px;
}

a {
  text-decoration: inherit;
  color: inherit;  
}


.header-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 60px;
    background-color:rgb(98, 162, 221);
    padding: 0 3%;
  }
  
  .header-icons {
    padding: 0 15px;
    font-size: 1.8em;
    color: #fff;
    cursor: pointer;
  }
  
.secondary-header-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 60px;
    background-color: #fff;
  }

  .secondary-header-menu {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    height: 100%;
  }

  .secondary-header-menu-items {
    margin: 0 40px;
    letter-spacing: 0.3px;
    color: rgb(146, 146, 146);
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 3px solid transparent;
  }

  .secondary-header-menu-items:hover {
    cursor: pointer;
  }

  .is-active {
    /* background-color: orange; */
    border-bottom: 3px solid rgb(98, 162, 221);
  }
.news-component {
    grid-area: news;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    /* padding: 0 40px; */
   
}

.news-heading {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.news-heading h1 {
    margin-bottom: -15px;
}

.news-heading p {
    color: rgb(146, 146, 146);
}

.news-articles {
    height: 100%;
}

 .news-articles ul{
list-style-type: none;
-webkit-padding-start: 0;
        padding-inline-start: 0;
display: -webkit-flex;
display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
-webkit-justify-content: space-between;
        justify-content: space-between;
height: 70%;
}


.news-article {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 25px;
    transition: 0.5s ease;
 }

 .news-article:hover {
    background-color: rgb(241, 241, 241);
    cursor: pointer;
    /* transform: scale(1.03); */
    transition: 0.5s ease;
 }


 .article-title {
 font-size: 13px;
 font-weight: 800;
 }

 .article-meta {
     font-size: 12px;
     color: rgb(177, 174, 174);
     padding-top: 5px;
 }

 .article-meta p {
     margin-top: 2px;
 }

.news-img {
    width: 110px;
    height: 70px;
    padding-right: 15px;
}

.more-option {
    margin-top: -50px;
}
.weather-component {
    grid-area: weather;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color:rgb(98, 162, 221);
    color: #fff;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

.weather-description {
    margin-top: -20px;
    margin-bottom: 25px;
}

.temperature-main {
    font-size: 50px;
    margin-top: 25px;
    margin-bottom: -10px;
}
.messages-component {
    grid-area: messages;
}

.messages-component h1, h3{
    text-align: center;
}
.message {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    /* border: 1px solid #000; */
    /* height: 80px; */
    /* background-color: green; */
    /* padding: 5px; */
    margin: 8px;
    transition: 0.5s ease;
}

.message:hover {
   background-color: rgb(241, 241, 241);
   cursor: pointer;
   /* transform: scale(1.02); */
    transition: 0.5s ease;
}

.sender-image {
    width: 80px;
    position: relative;
    top: 3px;
}

.sender-image img{
    width: 60px;
    border-radius: 50%;
}

.email-item {
    /* background-color: green; */
    width: 80%;
    padding-left: 10px;
}

.from, .email, .subject, .message-item, .email-date {
    margin: 2px 10px;
}

.email {
    font-size: 12px;
}

.subject, .message-item {
    font-size: 14px;
}

.subject {
    color: grey;
}

.email-date {
    /* position: relative;
    left: 12%;
    bottom: 10px; */
    font-weight: 800;
    font-size: 12px;
    /* background-color: greenyellow; */
    width: 50px;
}


